export default {
  props: ['goodsAttr'],
  data () {
    return {
      colorIndex: 0,
      ramIndex: 0
    };
  },
  components: {},
  methods: {
    checkColorEvent (num) {
      this.colorIndex = num;
    },
    checkRamEvent (num) {
      this.ramIndex = num;
    },
    submitBuyInfo () {
      var ram = this.goodsAttr.attr.ram[this.ramIndex];
      var color = this.goodsAttr.attr.color[this.colorIndex];
      return {ram: ram, color: color};
    }
  }
};



